.navbar {
  display: -ms-flexbox;
  display: flex;
  background: -webkit-gradient(linear,left top, left bottom,from(rgba(0, 0, 0, 1)),color-stop(rgba(0, 0, 0, .9)),to(rgba(0, 0, 0, 0)));
  background: -webkit-linear-gradient(rgba(0, 0, 0, 1),rgba(0, 0, 0, .9),rgba(0, 0, 0, 0));
  background: -o-linear-gradient(rgba(0, 0, 0, 1),rgba(0, 0, 0, .9),rgba(0, 0, 0, 0));
  background: linear-gradient(rgba(0, 0, 0, 1),rgba(0, 0, 0, .9),rgba(0, 0, 0, 0));
  /* height: 3em; */
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  -webkit-box-shadow: none;
          box-shadow: none;
  padding-top:2%;
}

.navbar ul {
  display: -ms-flexbox;
  display: flex;
  list-style: none;
  width: 90vw;
  margin: 0;
  /* height: 3em; */
  -ms-flex-align: center;
      align-items: center;
  font-family: 'Quicksand', sans-serif;
}

.navbar li a {
  text-decoration: none;
  margin: 0 1em;
  color: white;
  -webkit-transition: 0.5s;
  -o-transition: 0.5s;
  transition: 0.5s;
}

.navbar li a:hover {
  background: transparent;
  color: black;
  -webkit-transition: 0.5s;
  -o-transition: 0.5s;
  transition: 0.5s;
}




